@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;700&display=swap);
.container {
  max-width: 140rem;
  margin: auto;
  overflow: hidden;
  padding: 0 2rem;
}

.movie-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

/* Navbar */
.navbar {
  display: flex;
  justify-content: center;
  background-color: blueviolet;
  color: #fff;
  padding: 1rem 2rem;
  z-index: 1;
}

.navbar h1 {
  font-size: 4rem;
}

.search {
  text-align: center;
  margin-top: 4rem;
  margin-bottom: 3rem;
}

.search i {
  margin-right: -4rem;
  position: relative;
  z-index: 1;
  color: #777;
  font-size: 2rem;
}

.search__input {
  font-family: inherit;
  font-size: 2rem;
  margin-top: 3rem;
  margin: 0 auto;
  border-radius: 10rem;
  padding: 1.5rem 3rem;
  padding-left: 4.5rem;
  background-color: rgb(223, 247, 255);
  width: 20%;
  letter-spacing: 0.05rem;
  border: 1px solid blueviolet;
  transition: all 0.2s;
}

.search__input:focus {
  width: 30%;
  outline: none;
  color: #000;
}

.search__input::-webkit-input-placeholder {
  letter-spacing: 0.03rem;
  font-weight: 100;
  color: #000;
}

.search__input:-ms-input-placeholder {
  letter-spacing: 0.03rem;
  font-weight: 100;
  color: #000;
}

.search__input::-ms-input-placeholder {
  letter-spacing: 0.03rem;
  font-weight: 100;
  color: #000;
}

.search__input::placeholder {
  letter-spacing: 0.03rem;
  font-weight: 100;
  color: #000;
}

.loader {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.8);
}

.loader img {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.movie {
  background-color: #373b69;
  width: 30rem;
  margin: 1.5rem;
  border-radius: 3px;
  box-shadow: 0 -3px 5px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: all 0.2s;
}

.movie:hover {
  transform: translateY(-3px);
}

.movie img {
  object-fit: cover;
  height: 450px;
  width: 100%;
  border-radius: 5px;
}

.movie-info {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;

  padding: 1rem;
  color: #fff;
}

.movie-info h3 {
  font-size: 2rem;
  color: #ddd;
  margin-left: 1rem;
  margin-right: 1rem;
  padding-right: 1rem;
}

.tag {
  background-color: #22254b;
  border-radius: 5px;
  padding: 0.5rem;
  font-weight: bold;
}

.tag.green {
  color: rgb(9, 151, 9);
}

.tag.orange {
  color: orange;
}

.tag.red {
  color: red;
}

.movie-item {
  padding: 5rem;
  display: flex;
  justify-content: space-between;
}

.back-btn {
  color: #777;
  font-size: 2rem;
  border: 1px solid #000;
  border-radius: 5px;
  padding: 1rem 2rem;
  margin-top: 2rem;
  margin-left: 3rem;
  background-color: whitesmoke;
  display: inline-block;
}

.back-btn i {
  margin-right: 0.2rem;
}

.movie-item__image-container {
  box-shadow: 0 0.75rem 1rem rgba(0, 0, 0, 0.2);
}

.movie-item__image {
  flex: 1 1;
  width: 40rem;
  height: 50rem;
}

.movie-item__info {
  padding: 3rem;
  flex: 1 1;
}

.movie-item span {
  color: brown;
}

.movie-item__info h2 {
  color: blue;
  font-size: 5rem;
  margin-bottom: 2rem;
}

.movie-item__info p {
  font-size: 2rem;
  margin-bottom: 2rem;
}

/* @import url('https://fonts.googleapis.com/css?family=Roboto'); */

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  font-family: 'Lato', sans-serif;
  font-size: 1.6rem;
  line-height: 1.6;
  color: #333;
  background-color: #fff;
  font-weight: 700;
  background-color: #ddd;
}

a {
  text-decoration: none;
  color: #fff;
}

